* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Outfit, sans-serif;
}

html {
  max-height: 100%;
}

body {
  background-color: #f3e6d8;
  height: 100%;
  animation: 1s ease-in-out animate;
}

main {
  background-color: #fff;
  height: 100%;
}

h1 {
  color: #854632;
  margin: 1rem 0;
  font-family: Young Serif, serif;
  font-weight: 400;
}

header img {
  width: 100%;
  height: 100%;
}

header .heading {
  margin: 2rem;
}

header .heading h1 {
  color: #302d2c;
  font-size: 2.3rem;
  line-height: 1;
}

header .heading p {
  color: #5f574e;
  margin-bottom: 2.5rem;
  font-size: 1rem;
}

header .preparation {
  background-color: #fff5fa;
  border-radius: 10px;
  padding: 1.5rem 2rem;
}

header .preparation h2 {
  color: #7b284f;
  margin-bottom: 1rem;
}

header .preparation ul {
  color: #5f574e;
  padding-left: 2rem;
}

header .preparation ul li {
  padding-left: 1rem;
}

header .preparation ul li span {
  font-weight: 600;
}

header .preparation ul li:not(li:first-child) {
  line-height: 2;
}

header .preparation ul li::marker {
  color: #7b284f;
}

section {
  border-bottom: 1px solid #e4ded8;
  margin: 2rem;
  padding-bottom: 2rem;
}

section h1 {
  font-size: 1.8rem;
}

section ul {
  color: #5f574e;
  padding-left: 1.5rem;
}

section ul li {
  padding-left: 1rem;
  line-height: 1.5;
}

section ul li:not(li:last-child) {
  line-height: 2.5;
}

section ul li::marker {
  color: #854632;
}

.instructions-section {
  padding-bottom: 1rem;
}

.instructions-section ol {
  color: #5f574e;
  padding-left: 1.5rem;
}

.instructions-section ol li {
  padding-bottom: .5rem;
  padding-left: 1rem;
  line-height: 1.5;
}

.instructions-section ol li span {
  font-weight: 600;
}

.instructions-section ol li::marker {
  color: #854632;
  font-weight: 700;
}

.nutrition-section {
  border-bottom: none;
  padding-bottom: 0;
}

.nutrition-section h1 {
  margin: 1rem 0;
}

.nutrition-section p {
  color: #5f574e;
  margin-bottom: 1.5rem;
  padding-right: 1rem;
  font-size: 1rem;
}

.nutrition-section table {
  border-collapse: collapse;
  width: 100%;
}

.nutrition-section table tr {
  border-bottom: 1px solid #e4ded8;
  line-height: 2.5;
}

.nutrition-section table tr th {
  color: #5f574e;
  padding-right: 1rem;
  font-weight: 400;
}

.nutrition-section table tr td {
  color: #854632;
  font-weight: 700;
}

.nutrition-section table tr:last-child {
  border-bottom: none;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: translateY(-30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (width >= 768px) {
  body {
    justify-content: center;
    align-items: center;
    padding: 7rem 0;
    display: flex;
  }

  main {
    background-color: #fff;
    border-radius: 25px;
    width: 700px;
    min-height: 100%;
    padding: 2rem;
  }

  header img {
    border-radius: 15px;
  }

  header .heading, section {
    margin: 2rem 0;
  }

  table tr th {
    text-align: left;
    width: 50%;
    padding-left: 2rem;
    padding-right: 0;
  }

  table tr td {
    padding-left: 1rem;
  }
}

